import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from './../../services/authentication.service';
import $ from 'jquery';


@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {


  leftSize: number;
  rightSize: number;
  showMenu: boolean = false;

  constructor(private auth: AuthenticationService,
    private router: Router) { }




  manuToggle() {

    this.showMenu = !this.showMenu;

    if (this.showMenu == false) {

      // manuToggle(){
      //   this.showMenu = !this.showMenu;

      //   if(this.showMenu == false){      
      //     this.leftSize=2;
      //     this.rightSize=10;


      if ($(window).width() < 992) {
        this.rightSize = 12;


      }
      else if ($(window).width() < 1200) {
        // alert('1199');
        this.leftSize = 3;
        this.rightSize = 9;
      }
      else {
        // alert('big');
        this.leftSize = 2;
        this.rightSize = 10;

      }

      $('#firstdiv').removeClass("small-aside");
      $('#seconddiv').removeClass("big-right-side");


    }
    else {


      if ($(window).width() < 992) {
        this.rightSize = 12;
        // alert('first');
      }
      else if ($(window).width() < 1200) {
        // alert('1199');
        this.leftSize = 1;
        this.rightSize = 11;
      }
      else {
        // alert('big');
        this.leftSize = 1;
        this.rightSize = 11;

      }






      $('#firstdiv').addClass("small-aside");
      $('#seconddiv').addClass("big-right-side");
    }
  }


  hide_mobile_menu() {
    $('#close-show-991').click(function () {

      $('#firstdiv').removeClass("small-aside");
    })
  }




  showMenuMob = true;
  mobileManuToggle() {

    $('#firstdiv').addClass("small-aside");
    // this.showMenuMob = !this.showMenuMob;

  }
  ionViewWillEnter() {



    if ($(window).width() < 992) {
      this.rightSize = 12;
    }
    else if ($(window).width() < 1200) {
      this.leftSize = 3;
      this.rightSize = 9;
    }

    else {
      this.leftSize = 2;
      this.rightSize = 10;
    }
  }
  logout() {


    this.auth.signout().subscribe(
      (data) => {
        localStorage.clear();
        this.router.navigateByUrl('/login');
      },
      (error) => {
        console.log(error);
      }
    )
  }

}
